
.mainNav{
    background-color: #FF5733;
    height: 8vh;
    color: white;
    
}
.navItems{
    margin: 0;
    padding: 0;
    list-style: none;
}
.navItems li{
    display: inline-block;
}
.navItems li a{
    color: white;
    padding: 15px 10px;
    display: block;
   text-decoration: none;
   font-size: 1.2rem;
}
.navItems li a:hover{
background-color: brown;
}
.logo{
   
    width: 5vw;
    height: 3.8rem;
}

.hambtn{
    display: none;
}

@media only screen and (max-width:480px){
    .logo{
        width: 20vw;
        height: 2.5rem;
    }
    .hambtn{
        display: block;
        background: transparent;
        border: none;
        position: absolute;
        top: 0.4rem;
        right: 0.8rem;
    }
    .hamImg{
        transform: rotate(90deg);
    }
 
    .deskPages{
        display: none;
    }

    .resDiv{
        position: relative;
        top: 0.1rem;
        z-index: 9999;
        background-color: #FF5733;
        border-top: 1px solid white;
    }
    .resDiv li{
        height: 5vh;
        border-bottom: 1px solid white;
    }
    .resAnchors{
        list-style: none;

    }
    .resAnchors li a{  
      text-decoration: none;
      display: block;
      color: rgb(255, 255, 255);
      font-size: 1rem;
      margin-left: 10%;
    }
    .resAnchors li a:active{
        background-color:brown ;
    }
    #logo{
        margin-top: 0.6rem;
        font-size: 0.9rem;
    }
}