.carousel{
    max-width: 100vw;
    height: auto;

}
.heading{
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    position: absolute;
    top: 10rem;
    left: 2rem;
    width: 30%;
    background-color: rgba(236, 112, 99,0.5);
    color: white;
}
.heading h2::after{
    content: " ";
    background-color: rgb(241, 235, 235);
    display: block;
    width: 100px;
    height: 3px;
    
}
.alignSection{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 2rem;
    padding-bottom: 2rem;
    text-align: center;
}
.clientsAbout{
    background-color: rgba(0, 0, 0,0.9);
    color: white;
    height: 50vh;
}
.clientPic{
    width: 5rem;
    height: 5rem;
    border-radius: 10rem;
    border: 1px solid white;
}
.sectionUnder{
    width: 10rem;
    height: 3px;
    background-color: #FF5733;
}




@media only screen and (max-width: 480px) {
    .heading{
        padding-left: 1rem;
        padding-right: 2rem;
        display: flex;
        flex-direction: column;
        align-items: center;
        font-size: 0.7rem;
        text-align: center;
        position: absolute;
        word-wrap: break-word;
        top: 10rem;
        left: 0rem;
        width: 100%;
        background-color: rgba(236, 112, 99,0.5);
        color: white;
    }
    .section{
        font-size: 1rem;
        
    }
    .clientPic{
        width: 3rem;
        height: 3rem;
    }
    .clientsAbout{
        overflow: scroll;
    }
 .whyUs{
    font-size: .7rem;
 }
}