body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.carousel{
    max-width: 100vw;
    height: auto;

}
.heading{
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    position: absolute;
    top: 10rem;
    left: 2rem;
    width: 30%;
    background-color: rgba(236, 112, 99,0.5);
    color: white;
}
.heading h2::after{
    content: " ";
    background-color: rgb(241, 235, 235);
    display: block;
    width: 100px;
    height: 3px;
    
}
.alignSection{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 2rem;
    padding-bottom: 2rem;
    text-align: center;
}
.clientsAbout{
    background-color: rgba(0, 0, 0,0.9);
    color: white;
    height: 50vh;
}
.clientPic{
    width: 5rem;
    height: 5rem;
    border-radius: 10rem;
    border: 1px solid white;
}
.sectionUnder{
    width: 10rem;
    height: 3px;
    background-color: #FF5733;
}




@media only screen and (max-width: 480px) {
    .heading{
        padding-left: 1rem;
        padding-right: 2rem;
        display: flex;
        flex-direction: column;
        align-items: center;
        font-size: 0.7rem;
        text-align: center;
        position: absolute;
        word-wrap: break-word;
        top: 10rem;
        left: 0rem;
        width: 100%;
        background-color: rgba(236, 112, 99,0.5);
        color: white;
    }
    .section{
        font-size: 1rem;
        
    }
    .clientPic{
        width: 3rem;
        height: 3rem;
    }
    .clientsAbout{
        overflow: scroll;
    }
 .whyUs{
    font-size: .7rem;
 }
}
.footer{
    background-color: black;
    color: white;
}
.media{
    list-style: none;
}
.media li a{
    text-decoration: none;
    color: white;
    font-size: 1.2rem;
}
.findUs{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
}
.footerSection{
    padding-top: 2rem;
    padding-bottom: 2rem;

}
.footerContacts{
    font-size: 1.2rem;
    list-style: none;
}
.copyRite{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: rgb(20, 20, 20);
    height: 7vh;

}
#facebook:hover{
    color: rgb(39, 39, 134);
}
#facebook a:hover{
    color: rgb(39, 39, 134);
}
#instagram:hover{
    color: rgb(207, 51, 111);
}
#instagram a:hover{
    color: rgb(207, 51, 111);
}
#twitter:hover{
  color: rgb(55, 146, 189);
}
#twitter a:hover{
  color: rgb(55, 146, 189);
}
@media only screen and (max-width: 480px) {
    .copyRite{
        font-size: 0.6rem;
    }
    .footerContacts{
        font-size: 0.9rem;
    }
}

.mainNav{
    background-color: #FF5733;
    height: 8vh;
    color: white;
    
}
.navItems{
    margin: 0;
    padding: 0;
    list-style: none;
}
.navItems li{
    display: inline-block;
}
.navItems li a{
    color: white;
    padding: 15px 10px;
    display: block;
   text-decoration: none;
   font-size: 1.2rem;
}
.navItems li a:hover{
background-color: brown;
}
.logo{
   
    width: 5vw;
    height: 3.8rem;
}

.hambtn{
    display: none;
}

@media only screen and (max-width:480px){
    .logo{
        width: 20vw;
        height: 2.5rem;
    }
    .hambtn{
        display: block;
        background: transparent;
        border: none;
        position: absolute;
        top: 0.4rem;
        right: 0.8rem;
    }
    .hamImg{
        -webkit-transform: rotate(90deg);
                transform: rotate(90deg);
    }
 
    .deskPages{
        display: none;
    }

    .resDiv{
        position: relative;
        top: 0.1rem;
        z-index: 9999;
        background-color: #FF5733;
        border-top: 1px solid white;
    }
    .resDiv li{
        height: 5vh;
        border-bottom: 1px solid white;
    }
    .resAnchors{
        list-style: none;

    }
    .resAnchors li a{  
      text-decoration: none;
      display: block;
      color: rgb(255, 255, 255);
      font-size: 1rem;
      margin-left: 10%;
    }
    .resAnchors li a:active{
        background-color:brown ;
    }
    #logo{
        margin-top: 0.6rem;
        font-size: 0.9rem;
    }
}
.marquee{
    background-color: rgba(230, 93, 52,0.5);
    color: white;
}
.galleryImages{
    width: 100%;
    margin: 2.5%;
    height: auto;
    border: 3rem solid #FF5733;
}
.back{
    background-color: rgba(240, 34, 20, 0.212);
}

.Pimages{
    width: 100%;
    height: auto;
}
.Pimages:hover{
    box-shadow: 0px 0px 5px 3px black;
}
.brands{
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #FF5733;
    color: white;
    margin: 2%;
}
.owner{
    background-color: rgba(240, 69, 26,0.2);
}
.manager{
    background-color: rgba(240, 69, 26,0.2);
}
.staffPics{
    width: 100%;
    height: auto;
    border: 1px solid black;
    border-radius: 5rem;
    z-index: 1;
}
.waterMark{
    position: absolute;
    width: 100%;
    height: auto;
    top: 30%;
    left: 5%;
    opacity: 0.3;

}
