.footer{
    background-color: black;
    color: white;
}
.media{
    list-style: none;
}
.media li a{
    text-decoration: none;
    color: white;
    font-size: 1.2rem;
}
.findUs{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
}
.footerSection{
    padding-top: 2rem;
    padding-bottom: 2rem;

}
.footerContacts{
    font-size: 1.2rem;
    list-style: none;
}
.copyRite{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: rgb(20, 20, 20);
    height: 7vh;

}
#facebook:hover{
    color: rgb(39, 39, 134);
}
#facebook a:hover{
    color: rgb(39, 39, 134);
}
#instagram:hover{
    color: rgb(207, 51, 111);
}
#instagram a:hover{
    color: rgb(207, 51, 111);
}
#twitter:hover{
  color: rgb(55, 146, 189);
}
#twitter a:hover{
  color: rgb(55, 146, 189);
}
@media only screen and (max-width: 480px) {
    .copyRite{
        font-size: 0.6rem;
    }
    .footerContacts{
        font-size: 0.9rem;
    }
}