#goBackBtn{
    background-color: #FF5733;
    border-radius: 10rem;
    width: 30%;
    height: 5vh;
   margin-top: 2%;
   border: none;
}
#goBackBtn:hover{
 box-shadow: 2px 0px 5px 0px black;
}
#goBackBtn a{
text-decoration: none;
color: white;
font-size: 2rem;
height: 100%;    
display: flex;
justify-content: center;
align-items: center;
}
#goBackBtn a:hover{
letter-spacing: 0.3rem;
}

@media only screen and (max-width:480px){
    #goBackBtn a{

        font-size: 1rem;
 
        }
}
